import React, { useState } from "react";

import {
  AppBar,
  Avatar,
  Tab,
  Tabs,
  Toolbar,
  useMediaQuery,
  useTheme,
} from "@mui/material";

import { Link, animateScroll } from "react-scroll";

import DrawerComp from "./DrawerComp";

import { saveAs } from "file-saver";

const pages = [
  { name: "Home", link: "/", class: "home" },

  { name: "About", link: "about", class: "about" },

  { name: "Skills", link: "skills", class: "skills" },

  { name: "Projects", link: "projects", class: "projects" },

  { name: "Contact", link: "contact", class: "contact" },

  { name: "Resume", link: "bt", class: "resume" },
];

const onButtonClick = () => {
  saveAs(
    "https://drive.google.com/u/0/uc?id=1MpM0hx_z1orh_0doTLNW5BoU_FXJ_VIE&export=download"
  );
};

const Navbar = () => {
  const [value, setValue] = useState(0);

  const [head, setHead] = useState(false);

  const theme = useTheme();

  const isMatch = useMediaQuery(theme.breakpoints.down("md"));

  return (
    <AppBar sx={{ backgroundColor: "#063970", color: "white" }} id="nav-menu">
      <Toolbar>
        <a href="/">
          <Avatar src="./images.png" />
        </a>
        {isMatch ? (
          <>
            <DrawerComp setValue={setValue} />
          </>
        ) : (
          <Tabs
            sx={{ marginLeft: "auto", color: "white" }}
            textColor="inherit"
            value={value}
            indicatorColor="secondary">
            {pages.map((elem, index) =>
              elem.link !== "bt" ? (
                <Link
                  activeClass="active"
                  className={`nav-link ${elem.class}`}
                  key={index}
                  to={elem.link}
                  onClick={() => {
                    elem.link === "/"
                      ? animateScroll.scrollToTop()
                      : setHead(true);
                  }}
                  spy={true}
                  smooth={true}>
                  <Tab
                    label={elem.name}
                    sx={{
                      fontSize: "1.2rem",
                      color: "white",
                      fontWeight: "800",
                    }}
                    onClick={() => setValue(index)}
                  />
                </Link>
              ) : (
                <a
                  href="https://drive.google.com/file/d/1MpM0hx_z1orh_0doTLNW5BoU_FXJ_VIE/view"
                  target="_blank"
                  rel="noreferrer"
                  className={`nav-link ${elem.class}`}
                  style={{ textDecoration: "none" }}>
                  <Tab
                    id="resume-button-1"
                    label={elem.name}
                    sx={{
                      fontSize: "1.2rem",
                      color: "white",
                      fontWeight: "800",
                    }}
                    onClick={onButtonClick}
                  />
                </a>
              )
            )}
          </Tabs>
        )}
      </Toolbar>
    </AppBar>
  );
};

export default Navbar;
